@charset "UTF-8";
/*标题，抬头css*/
.webmain .con { margin-bottom: 45px; }

.webmain .con .title { height: 60px; overflow: hidden; background: url(../../images/line_contitle.gif) center center repeat-x; margin-bottom: 15px; position: relative; }

.webmain .con .title a.more { display: block; position: absolute; right: 0; top: 16px; background: url(../../images/more_contitle.gif); width: 130px; height: 30px; text-indent: -99em; overflow: hidden; }

.webmain .con .title h2 { display: block; width: 200px; height: 60px; background-image: url(../../images/p_starinfo_1.jpg); background-repeat: no-repeat; text-indent: -99em; overflow: hidden; float: left; }

.webmain .con .title ul { float: left; height: 26px; background: #fff; padding: 17px 5px 17px 5px; }

.webmain .con .title ul li { float: left; font-size: 16px; height: 26px; line-height: 26px; overflow: hidden; margin-right: 8px; }

.webmain .con .title ul li a { display: block; width: 100%; height: 26px; line-height: 26px; cursor: pointer; padding: 0 8px; }

.webmain .con .title ul li.c a { background: #000; color: #fff; }

/*导航栏*/
.navTitle { color: #ccc; margin-bottom: 30px; }

.navTitle a { color: inherit; }

.navTitle a:hover { color: red; }

.navTitle .loginfo { float: right; margin: 0 20px 0 15px; }

.navTitle .loginfo .face { display: inline-block; float: left; width: 20px; height: 20px; background: url("../../images/icon-logface.jpg") 50% no-repeat; }

.navTitle .loginfo .name { display: inline-block; float: left; height: 20px; margin-left: 15px; line-height: 20px; cursor: pointer; }

.navTitle .loginfo .name:after { content: ""; display: block; position: absolute; margin-left: 55px; margin-top: -10px; border-top: 6px solid #000; border-right: 6px solid #fff; border-left: 6px solid #fff; }

.introduction-face, .introduction-face img { width: 220px; height: 300px; }

.introduction-face { float: left; }

.introduction-content { margin-left: 455px; }
.introduction-content .name { font-size: 40px; }
.introduction-content .details { font-size: 14px; line-height: 30px; margin-top: 20px; }
.introduction-content .details .left-col { float: left; }
.introduction-content .details .right-col { margin-left: 330px; }

.des { display: inline-block; width: 80px; color: #ccc; }

.worklist-main .imglist { width: 550px; height: 770px; }

.worklist-main .tabsection li, .worklist-main .tabsection li img { width: 95px; height: 133px; }

.worklist-main { text-align: center; }
.worklist-main .workname { display: inline-block; font-size: 14px; }
.worklist-main .imglist { margin: 15px auto; }
.worklist-main .imglist li { display: none; }
.worklist-main .imglist li.active { display: block; }
.worklist-main .tabsection { position: absolute; margin-top: -780px; margin-left: 1105px; }
.worklist-main .tabsection .up { position: absolute; width: 95px; height: 27px; margin-top: -37px; background: url("../../images/sprite_starinfo_toggle.png"); }
.worklist-main .tabsection .down { display: inline-block; width: 95px; height: 27px; background: url("../../images/sprite_starinfo_toggle.png") 0 -27px no-repeat; }
.worklist-main .tabsection .frame { width: 89px; height: 127px; position: absolute; top: 0; border: 3px solid #9966cc; }
.worklist-main .tabsection li { margin-bottom: 10px; }

